import React from 'react'
import { Route, Switch } from 'react-router-dom'

import Bar from './Bar'
import SideBar from './SideBar'

const style = {
  width: 'inherit',
  margin: 20,
  boxShadow: '10px 12px 30px 0px #f1eded',
}

const routes = [
  {
    path: '/doctors/:id/personal-setting',
    component: Bar,
  },
  {
    path: '/doctors/:id/cancellation',
    component: Bar,
  },
  {
    path: '/doctors/:id/questionnaire',
    component: Bar,
  },
  {
    path: '/doctors/:id/work-schedules',
    component: Bar,
  },
]

const DocBody = () => {
  return (
    <div style={style}>
      <SideBar />
      <Switch>
        {routes.map((route, index) => {
          const Component = route.component

          return (
            <Route
              path={route.path}
              exact={route.exact}
              name={route.name}
              key={index}
            >
              <Component />
            </Route>
          )
        })}
      </Switch>
    </div>
  )
}

export default DocBody
